import { createSlice } from "@reduxjs/toolkit";

type Tab = "settings" | "fractal" | "palette" | "about" | "social" | "debug";
interface Int {
  drawer: boolean;
  infobox: boolean;
  mouseOnCanvas: boolean;
  narrowDevice: boolean; // less than 450 px
  tab: Tab;
  snack?: string;
  square: boolean; // should a square layer be displayed ('s' key)
  showPois: boolean;
  screen: {
    width: number;
    height: number;
    min: number;
  };
  mouse: {
    x: number;
    y: number;
    iter: number;
  };
}

const ui = createSlice({
  name: "ui",
  initialState: {
    drawer: false,
    mouseOnCanvas: false,
    tab: "fractal",
    infobox: false,
    square: false,
    showPois: true,
    mouse: { x: 0, y: 0, iter: 0 },
    narrowDevice: false,
    orientableDevice: false,
    screen: { width: 0, height: 0, min: 0 },
  } as Int,
  reducers: {
    setUi: (state, action) => ({ ...state, ...action.payload }),
    toggleSquare: state => ({ ...state, square: !state.square }),
    setDrawer: (state, action) => ({ ...state, drawer: action.payload }),
    setTab: (state, action) => ({ ...state, tab: action.payload }),
    setScreenSize: (state, action) => {
      const res = { ...state, screen: action.payload };
      res.screen.min = Math.min(res.screen.width, res.screen.height);
      return res;
    },
    setInfobox: (state, action) => ({ ...state, infobox: action.payload }),
    setMouseOnCanvas: (state, action) => ({
      ...state,
      mouseOnCanvas: action.payload,
    }),
    setMouseInfo: (state, action) => ({ ...state, mouse: action.payload }),
    setNarrowDevice: (state, action) => ({
      ...state,
      narrowDevice: action.payload,
    }),
    setSnack: (state, action) => ({ ...state, snack: action.payload }),
  },
});

export const { reducer, actions } = ui;
export const {
  setUi,
  setDrawer,
  setTab,
  setInfobox,
  setMouseOnCanvas,
  setMouseInfo,
  setSnack,
  setNarrowDevice,
  toggleSquare,
  setScreenSize,
} = actions;
